import Index from "../component/page/index.vue";
import Order from "../component/page/order.vue";
import RechargeRecord from "../component/page/rechargeRecord.vue";
import Machine from "../component/page/machine.vue";
import Complaint from "../component/page/complaint.vue";
import MemberCentre from "../component/page/memberCentre.vue";
import HighDefense from "../component/page/highDefense.vue";

export const menu_router = [
    {
        router: "?business_sn=0#/order/0",
        menu: "5-1",
        mode: "loose"
    },
    {
        router: "#/machine/zhuyong",
        menu: "2-1"
    },
    {
        router: "#/machine/tuoguan",
        menu: "2-2"
    },
    {
        router: "#/machine/jigui",
        menu: "2-3"
    },
    {
        router: "#/complaint",
        menu: "3-1"
    },
    {
        router: "#/highdefense",
        menu: "4-1"
    }
];

export const byMenuToUrl = (menu) => {
    location.href = menu_router.find(item => item.menu == menu).router;
}

export default [
    { path: '/order/:business_sn', component: Order },
    { path: '/rechargeRecord', component: RechargeRecord },
    { path: '/machine/:type', component: Machine },
    { path: '/complaint', component: Complaint },
    { path: '/membercentre', component: MemberCentre },
    { path: '/highdefense', component: HighDefense },
    { path: '/', component: Index },
];