<style scoped>
    .page {
        margin-top: 10px;
        float: left;
    }
    .clear:before,.clear:after {
        content:"";
        display:table;
    }
    .clear:after { 
        clear:both;
     }
     .operat {
         text-align: right;
         margin-bottom: 10px;
     }
</style>
<template>
    <div>
        <div class="operat">
            <Button type="primary">创建高防IP</Button>
        </div>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
    </div>
</template>
<script>
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '套餐名称',
                        key: 'package_name'
                    },
                    {
                        title: '所属机房',
                        key: 'machine_room'
                    },
                    {
                        title: 'IP地址',
                        key: 'ip'
                    },
                    {
                        title: '防御流量',
                        key: 'defense'
                    },
                    {
                        title: '到期时间',
                        key: 'end_time'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                           return h('div',[h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        alert("续费成功");
                                    }
                                }
                            }, '续费')]);
                        }
                    }
                ],
                data: [
                    {
                        package_name: "套餐一",
                        machine_room: "西安机房",
                        ip: "127.0.0.1",
                        defense: "50GB",
                        end_time: "2019-01-20"
                    }
                ],
                allData: [
                    {
                        package_name: "套餐一",
                        machine_room: "西安机房",
                        ip: "127.0.0.1",
                        defense: "50GB",
                        end_time: "2019-01-20"
                    }
                ],
                page: 0,
                pageTotal: 11
            }
        },
        methods: {
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            }
        }
    }
</script>