<style scoped>
    .page {
        margin-top: 10px;
        float: left;
    }
    .clear:before,.clear:after {
        content:"";
        display:table;
    }
    .clear:after { 
        clear:both;
     }
</style>
<template>
   <div>
        <Table :columns="columns" :data="data"></Table>
        <Page class="page" @on-change="toPage" :total="allData.length" />
        <Modal
            v-model="renewal_fee"
            title="续费服务器"
            @on-ok="ok"
            @on-cancel="cancel">
            <Select placeholder="请选择续费时长" v-model="renewal_fee_config.price" style="width:100%">
                <Option v-for="item in renewal_fee_config.price_options" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <Input v-model="renewal_fee_config.order_note" type="textarea" placeholder="备注" />
        </Modal>
   </div>
</template>
<script>
    import axios from "axios";
    import { BASE_URL, PROTOCOL, MACHINE_TYPE } from "../../config/index.js";
    export default {
        data () {
            return {
                renewal_fee: false,
                renewal_fee_config: {
                    price: 1,
                    order_note: "",
                    price_options: [
                        {
                            value: 1,
                            label: "一个月"
                        },
                        {
                            value: 6,
                            label: "半年"
                        },
                        {
                            value: 12,
                            label: "一年"
                        }
                    ],
                    renewal_fee_param: {

                    }
                },
                columns: [
                    {
                        title: '业务编号',
                        key: 'business_number'
                    },
                    {
                        title: '机器编号',
                        key: 'machine_number'
                    },
                    {
                        title: 'IP',
                        key: 'ip'
                    },
                    {
                        title: '单价',
                        key: 'money'
                    },
                    {
                        title: '业务状态',
                        key: 'business_status'
                    },
                    {
                        title: '到期时间',
                        key: 'endding_time'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            let operat = [
                                h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.show(params.row);
                                    }
                                }
                                }, '查看')
                            ];
                            operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.renewal_fee = true;
                                            this.renewal_fee_config.renewal_fee_param = params.row;
                                        }
                                    }
                            }, '续费'));
                            operat.push(h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            location.href = "/tz/?business_sn="+params.row.business_number+"#order/"+params.row.business_number;
                                        }
                                    }
                            }, '订单'));
                            return h('div',operat);
                        }
                    }
                ],
                data: [
                ],
                allData: [],
                page: 0,
                pageTotal: 11,
            };
        },
        methods: {
            ok () {
                // this.$Message.info('Clicked ok');
                axios.post(PROTOCOL+"//"+BASE_URL+"/home/customer/renewresource",{
                    business_number: this.renewal_fee_config.renewal_fee_param.business_number,
                    price: this.renewal_fee_config.renewal_fee_param.money,
                    length: this.renewal_fee_config.price,
                    resource_type: this.renewal_fee_config.renewal_fee_param.type,
                    order_note: this.renewal_fee_config.order_note
                }).then(res => {
                    if(res.data.code==1) {
                        alert(res.data.msg);
                        location.href = "/tz/?business_sn=" + this.renewal_fee_config.renewal_fee_param.business_number +"#order/"+this.renewal_fee_config.renewal_fee_param.business_number;
                        this.getData();
                    } else {
                        alert(res.data.msg);
                    }
                });
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            },
            toPage(page) {
                // console.log(page);
                let start = (page - 1) * this.pageTotal;
                let end = (page - 1) * this.pageTotal + this.pageTotal;
                this.data = this.allData.sort((a,b) => b.id - a.id).slice(start,end);
            },
            show (data) {
                this.$Modal.info({
                    title: '详细信息',
                    content: `
                        业务编号: ${data.business_number}<br />
                        业务类型: ${data.business_type}<br />
                        机器编号: ${data.machine_number}<br />
                        业务状态: ${data.business_status}<br />
                        CPU: ${data.resource_detail_json.cpu}<br />
                        内存: ${data.resource_detail_json.memory}<br />
                        硬盘: ${data.resource_detail_json.harddisk}<br />
                        带宽: ${data.resource_detail_json.bandwidth}<br />
                        防御: ${data.resource_detail_json.protect}<br />
                        单价: ${data.money}<br />
                        时长: ${data.length}<br />
                        备注: ${data.business_note}<br />
                        `
                })
            },
            getData() {
                if(sessionStorage.getItem("businessList")) {
                    const data = JSON.parse(sessionStorage.getItem("businessList"));
                    
                    this.allData = data.filter(item => item.type==MACHINE_TYPE[this.$route.params.type]).map(item => Object.assign(item,{
                        resource_detail_json: JSON.parse(item.resource_detail),
                        ip: JSON.parse(item.resource_detail).ip,
                    }));
                    this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                    console.log(this.allData);
                } else {
                    axios.get(PROTOCOL+"//"+BASE_URL+"/home/customer/businessList",{
                        type: MACHINE_TYPE[this.$route.params.type]
                    }).then(res => {
                        if(res.data.code==1) {
                            sessionStorage.setItem("businessList",JSON.stringify(res.data.data));
                            this.allData = res.data.data.filter(item => item.type==MACHINE_TYPE[this.$route.params.type]).map(item => Object.assign(item,{
                                resource_detail_json: JSON.parse(item.resource_detail),
                                ip: JSON.parse(item.resource_detail).ip,
                            }));
                            this.data = this.allData.sort((a,b) => b.id - a.id).slice(this.page,this.pageTotal);
                        }else if(res.data.code==5000) {
                            location.href = PROTOCOL+"//"+BASE_URL+"/tz/login.html";
                        } else {
                            console.error(res.data.msg);
                        }
                    });
                }
                 
            }
        },
        beforeRouteUpdate(to, from, next) {
            console.log(to, from);
            this.getData();
            next();
        },
        mounted() {
           this.getData();
        }
    }
</script>